

* {
  box-sizing: border-box;
}

body {
  font-family: 'Raleway', sans-serif;
}

h1, h2 {
  font-family: 'Permanent Marker', Helvetica, sans-serif;
}

.header {
  padding: 4rem;
}

.header__h1 {
  margin: auto 0;

  text-align: center;
}

.header__text {
  padding: 1rem;

  display: inline-block;
  background-color: #222;
  color: white;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 3rem;

  -webkit-transform: rotate(-3deg) skew(-3deg);

          transform: rotate(-3deg) skew(-3deg);
}

.board {
  display: flex;
  flex-wrap: wrap;
}

.validation-errors-display {
  text-align: center;
  margin-bottom: 2em;
}

.validation-errors-display__list {
  list-style-type: none;
  padding: 0;
}

.card {
  background-color: #F4FF81;

  padding: 0;
  margin: 0.5rem;

  min-height: 250px;
  min-width: 225px;
  flex-basis: 18%;

  border-radius: 5px;

  display: grid;
  grid-template-rows: 1fr 4fr 1fr;
  /* align-items: center; */
}

.card__content {
  /* grid-column-start: 2; */
  display: flex;
  flex-direction: column;
  align-self: center;

  text-align: center;
  font-weight: 100;
  overflow: scroll;

  margin: 0 30px;
  order: 2;
}

.card__content-text {
  text-overflow: ellipsis;
  overflow: scroll;
}

.card__content-emoji {
  font-size: 3rem;
}

.card__content-text, .card__content-emoji {
  padding: 0;
  margin: 0;
}

.card__tools {
  /* align-self: start; */
  display: flex;
  justify-content: space-between;
  order: 1;
  margin-left: 0.5em;
}

.card__tools-button {
  background-color: inherit;
  font-family: 'Permanent Marker', Helvetica, sans-serif;
  font-size: 1.2em;
  border: none;
  color: darkgray;
}

.card__tools-button:hover {
  color: black;
}

.card__tools-button:last-of-type {
  margin-left: auto;
}

/* The Modal (background) */

.new-card-form {
  /* width: 50%; */
  /* margin: auto;
  padding-bottom: 4rem; */
}

.new-card-form__header {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 1em;
}

.new-card-form__form {
  font-size: 1.2em;
  display: grid;
  grid-template-columns: [labels] 54px [controls] 100px;
  grid-auto-flow: row;
  grid-gap: 8px;
}

.new-card-form__form-label  {
  grid-column: labels;
  grid-row: auto;
}

.new-card-form__form-select,
.new-card-form__form-textarea,
.new-card-form__form-button {
  font-family: 'Raleway', sans-serif;
  font-size: 1.25em;
  grid-column: controls;
  grid-row: auto;
}

.new-card-form__form-button {
  margin-top: 1em;
  background-color: inherit;
  border: 1px solid black;
  font-size: 1em;
}

